import _ from "lodash";
import { useMemo, useState } from "react";

interface TExtended {
  [key:string]: {}[]
}

export function useSearchField<T extends any[] | TExtended>( data: T, fieldsToFilter: string[]): {
  filteredData: T;
  handleSearchInputChange: (query?: string) => void;
  isFiltering: boolean;
} {
  const [isFiltering, setIsFiltering] = useState<boolean>(false);
  const [filteredData, setFilteredData] = useState<T>(data);
  
  const handleSearchInputChange = (query?:string) => {
    setIsFiltering(query != null && query !== '');

    // TODO: typings
    if (_.isArray(data)) {
      let filterApplied:Array<T> = [...data];

      if (query != null && Array.isArray(data)) {
        filterApplied = _.filter(data, obj => {
          return _.some(fieldsToFilter, prop => {
            return String(_.get(obj, prop)).toLowerCase().includes(query.toLowerCase());
          });
        });
      }

      setFilteredData(filterApplied as T);

      return;
    }else if (_.isObject(data)) {
      const filterApplied:T = {...data};

      if (query != null) {
        // Iterar sobre cada propiedad del objeto principal
        _.forEach(data, (array, key) => {
          // Filtrar cada array de objetos según las propiedades anidadas y el valor específico
          const filteredArray = _.filter(array as TExtended, obj => {
            return _.some(fieldsToFilter, prop => {
              return String(_.get(obj, prop)).toLowerCase().includes(query.toLowerCase());
            });
          });

          (filterApplied as TExtended)[key] = filteredArray;
        });
      }

      setFilteredData(filterApplied as T);

      return;
    }

    // not an array, not an object
    setFilteredData(data as T);
  };

  // const [isFiltering, setIsFiltering] = useState<boolean>(false);
  // const [query, setQuery] = useState<string | undefined>(undefined);

  // const filteredData = useMemo(() => {
  //   setIsFiltering(query != null && query !== '');

  //   // TODO: typings
  //   if (_.isArray(data)) {
  //     let filterApplied:Array<T> = [...data];

  //     if (query != null && Array.isArray(data)) {
  //       filterApplied = _.filter(data, obj => {
  //         return _.some(fieldsToFilter, prop => {
  //           return String(_.get(obj, prop)).toLowerCase().includes(query.toLowerCase());
  //         });
  //       });
  //     }

  //     return filterApplied as T;
  //   }else if (_.isObject(data)) {
  //     const filterApplied:T = {...data};

  //     if (query != null) {
  //       // Iterar sobre cada propiedad del objeto principal
  //       _.forEach(data, (array, key) => {
  //         // Filtrar cada array de objetos según las propiedades anidadas y el valor específico
  //         const filteredArray = _.filter(array as TExtended, obj => {
  //           return _.some(fieldsToFilter, prop => {
  //             return String(_.get(obj, prop)).toLowerCase().includes(query.toLowerCase());
  //           });
  //         });

  //         (filterApplied as TExtended)[key] = filteredArray;
  //       });
  //     }

  //     return filterApplied as T;

  //   }

  //   // not an array, not an object
  //   return data as T;
  // }, [query]);

  // const handleSearchInputChange = (query?:string) => {
  //   setQuery(query);
  // }

  return { filteredData, handleSearchInputChange, isFiltering };
}